			],
			cateid: 0,
			keywords: '',
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			SwiperList: [],
			goods: [],
			noticeText: '',
			status: 'loadmore',
			list: 20,
