		}
	},
	methods: {
		async CateData() {
			//ÁªÑË£ÖÊï∞ÊçÆ
			var data = {}

			var result = await uni.$u.http.post('/post/cate', data)

			if (result.code == 0) {
				this.$refs.notice.show({
					type: "error",
					message: result.msg
				})
				return false
			}

			this.cate.push(...result.data)
		},
		async PostData() {
			//ÁªÑË£ÖÊï∞ÊçÆ
			var data = {
				cateid: this.cateid,
				keywords: this.keywords
			}

			var result = await uni.$u.http.post('/post/index', data)

			if (result.code == 0) {
				this.$refs.notice.show({
					type: "error",
					message: result.msg
				})
				return false
			}

			//Ë¶ÜÁõñÂÄº
			this.post = result.data
		},
		CateToggle(item) {
			this.cateid = item.id
			//Ê∏ÖÁ©∫Êï∞ÊçÆ
			this.post = []
			this.PostData()
		},
		search() {
			// this.post = []
			// this.PostData()
			console.log(111);

		},
		goSearch() {
			console.log(222);
			this.keywords = ''
			uni.navigateTo({
