				urls: this.SwiperList.map((item) => item.imgUrl)
			});

		},
	},
	onLoad() {
		this.CateData()
		this.PostData()
		uni.$u.http.get('page/detail',).then(res => {
			console.log(res, 'ÊâìÂç∞ÁªìÊûú');
			if (res.status == 200) {
				this.SwiperList = res.data.pageData.items[2].data
				this.goods = res.data.pageData.items[3].data
				this.noticeText = res.data.pageData.items[1].params.text
				console.log("üöÄ Â∫óÈã™ÂÖ¨Âëä", this.noticeText)
				console.log("üöÄ ËΩÆÊí≠ÂõæÂàóË°®", this.SwiperList)
				console.log("üöÄ ÊâÄÊúâÂïÜÂìÅÂàóË°®", this.goods)
				this.Firstgoods = this.goods.splice(0, 4)
				console.log('ÊèêÂèñÊï∞ÁªÑÂâç6È°π', this.Firstgoods);
				console.log("üöÄ ÊâÄÊúâÂïÜÂìÅÂàóË°®", this.goods)


			}
		})
	},
	onReachBottom() {
		console.log('ÊªöÂä®‰∏ãÊãâ');
		if (this.page >= 5) return;
		this.status = 'loading';
		this.page = ++this.page;
		setTimeout(() => {
			let a = this.goods.splice(0, 4)
			this.Firstgoods.push(...a)
			console.log('ÊèêÂèñÊï∞ÁªÑÂâç4È°π', this.Firstgoods);
			console.log("üöÄ ÊâÄÊúâÂïÜÂìÅÂàóË°®", this.goods)
